import { h } from 'virtual-dom';

function infoComponent() {
  return (
    h('div', [
      h('div', [
        h('b', ': '),
        h('span', '. -,  , . 15, . '),
      ]),
      h('div', [
        h('b', ': '),
        h('tel', '(812) 992-91-05'),
      ]),
      h('div', [
        h('span', '   '),
        h('a', { href: 'http://vk.com/nevafootball' }, 'http://vk.com/nevafootball'),
      ]),
    ])
  );
}

export default infoComponent;



